import React, { useState } from 'react'
import { gql } from 'apollo-boost'

const getPokemons = gql`
  {
    pokemons(first: 10) {
      id
      name
      types
      image
    }
  }
`

const Page = () => {

  const showPokemons = () => {
    return(

      <Query query={getPokemons}>
        {({ loading, error, data }) => {
          if (loading) return <p>Loading...</p>;
          if (error) return <p>Error :(</p>;

          return data.pokemons.map(pokemon => {
            return(
                <Pokemon key={pokemon.id} id={id}  
                  onClick={() => {Router.push({pathname: '/pokemonInfo',query: { id: pokemon.id }}), 
                  setId(pokemon.id)}} src={pokemon.image} name={ pokemon.name } 
                  type={pokemon.types}/>
            )
          })
        }}
    </Query> 
)}

  return <div>{showPokemons()}</div>
}

export default Page
